.range-slider {
  &__content {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin-bottom: 19px;
  }

  &__desc {
    margin-right: 8px;
  }

  &__percent {
    flex-shrink: 0;
    font-size: 18px;
  }
}

.noUi-target {
  border: 1px solid var(--color-white);
  background: rgba(255, 255, 255, 0.70);
  border-radius: 10px;
  box-shadow: none;
}

.noUi-horizontal {
  height: 6px;
}

.noUi-connect {
  background: rgba(255, 255, 255, 0);
}


.noUi-handle:before {
  display: none;
}

.noUi-handle {
  border: 1px solid var(--color-brand-hover);
  background: var(--color-brand-hover);
  border-radius: 50%;
  box-shadow: none;
  cursor: pointer;
}

.noUi-horizontal .noUi-handle {
  top: -10px;
  right: -16px;
  width: 26px;
  height: 26px;
}

.noUi-handle:after,
.noUi-handle:before {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 20px;
  height: 20px;
  background: var(--color-brand);
  border-radius: 50%;
  content: '';
  transform: translate(-50%, -50%);
}

.noUi-target.noUi-horizontal .noUi-base {
  width: calc(100% - 22px);
  transform: translateX(8px);
}


