@use "../../../scss/general/variables";

.burger {
  position: relative;
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  margin: 0;
  cursor: pointer;
  transition-duration: 1s;

  &__line {
    position: absolute;
    left: 10px;
    width: 20px;
    height: 2px;
    background-color: var(--color-white);
    border-radius: 20px;
    transition-delay: 0.25s;
    transition-duration: 0.25s;

    &:before {
      position: absolute;
      top: -6px;
      left: 0;
      width: 20px;
      height: 2px;
      background-color: var(--color-white);
      border-radius: 20px;
      content: "";
      transition: transform 0.25s, top 0.25s 0.25s;
      transition-duration: 0.25s;
    }

    &:after {
      position: absolute;
      top: 6px;
      left: 0;
      width: 20px;
      height: 2px;
      background-color: var(--color-white);
      border-radius: 20px;
      content: "";
      transition: transform 0.25s, top 0.25s 0.25s;
      transition-duration: 0.25s;
    }
  }

  &.is-active &__line {
    background: transparent;
    transition-delay: 0.25s;
    transition-duration: 0.1s;
  }

  &.is-active &__line:before {
    top: 0;
    transform: rotateZ(-45deg);
    transition: top 0.25s, transform 0.25s 0.25s;
  }

  &.is-active &__line:after {
    top: 0;
    transform: rotateZ(45deg);
    transition: top 0.4s, transform 0.25s 0.25s;
  }

  @media (min-width: variables.$lg) {
    display: none;
  }
}
