@use "../../scss/general/variables";

.footer {
  background-color: var(--color-black-lightest);

  &__wrapper {
    display: flex;
    flex-direction: column-reverse;
    padding: 16px 0;

    @media (min-width: variables.$lg) {
      flex-direction: row;
      justify-content: space-between;
      padding: 30px 0;
    }
  }

  &__group {
    display: flex;
    flex-direction: column;
    justify-content: center;

    &:not(:last-child) {
      margin-top: 24px;
    }

    @media (min-width: variables.$sm) {
      flex-direction: row;
    }

    @media (min-width: variables.$lg) {
      &:not(:last-child) {
        margin-top: 0;
      }
    }
  }

  &__line {
    display: none;

    @media (min-width: variables.$lg) {
      display: block;
      width: 1px;
      height: auto;
      margin: 0 4px;
      background-color: var(--color-gray);

      &:first-of-type,
      &:last-of-type {
        display: none;
      }
    }
  }

  &__copy {
    font-size: 13px;
    text-align: center;

    @media (min-width: variables.$lg) {
      text-align: left;
    }
  }
}
