@use "../../scss/general/variables";

.hero {
  position: relative;
  overflow: hidden;
  color: var(--color-black);

  &__bg-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__bg {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__content {
    position: relative;
    z-index: 1;
    display: flex;
    width: 100%;
    max-width: 780px;
    height: 100%;
    flex-direction: column;
    justify-content: center;
    padding: 120px 0;

    @media (min-width: variables.$lg) {
      padding: 200px 0;
    }
  }

  &__title {
    margin-bottom: 56px;
    font-size: 28px;
    font-weight: 700;
    text-transform: uppercase;

    @media (min-width: variables.$sm) {
      font-size: 36px;
    }

    @media (min-width: variables.$lg) {
      margin-bottom: 90px;
      font-size: 48px;
    }
  }

  &__heading {
    margin-bottom: 10px;
    font-size: 20px;
    font-weight: 700;
  }

  &__item {
    position: relative;
    padding-left: 26px;
    font-size: 20px;

    &::before {
      position: absolute;
      top: -1px;
      left: 10px;
      content: "-";
      font-size: 21px;
    }

    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__actions {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 56px;

    @media (min-width: variables.$md) {
      flex-direction: row;
    }

    @media (min-width: variables.$lg) {
      margin-top: 80px;
    }
  }

  &__btn {
    &:not(:last-child) {
      margin-bottom: 12px;
    }

    @media (min-width: variables.$md) {
      &.btn {
        max-width: 183px;
      }

      &:not(:last-child) {
        margin-right: 30px;
        margin-bottom: 0;
      }
    }
  }
}
