@use "../../scss/general/variables";

.nav {
  position: absolute;
  z-index: 9;
  top: 72px;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: calc(100vh - 72px);
  flex-direction: column;
  padding: 0 16px;
  background-color:  var(--color-black-light);
  overflow-y: auto;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;

  &.is-active {
    transform: translateX(0);
  }

  @media (min-width: variables.$lg) {
    position: relative;
    top: 0;
    height: initial;
    flex-direction: row;
    align-items: center;
    padding: 0;
    background-color: initial;
    overflow-y: initial;
    transform: translateX(0);
  }


  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 32px 0;

    @media (min-width: variables.$lg) {
      flex-direction: row;
      padding: 0;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 16px;
    }

    @media (min-width: variables.$lg) {
      &:not(:last-child) {
        margin-right: 60px;
        margin-bottom: 0;
      }
    }
  }

  &__link {
    position: relative;
    color: var(--color-white);
    font-size: 13px;
    font-weight: 600;
    text-transform: uppercase;
    transition: color 0.3s ease-in-out;

    &::after {
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: var(--color-brand);
      content: "";
      transform: scaleX(0);
      transform-origin: right;
      transition: transform 0.3s ease-in-out;
    }

    &:hover,
    &:focus {
      color: var(--color-brand);
    }

    &:hover::after {
      transform: scaleX(1);
      transform-origin: left;
    }
  }
}
