.simplebar-track.simplebar-vertical {
  width: 7px;
  background-color: var(--color-black-lightest);
}

.simplebar-scrollbar::before {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--color-brand);
  opacity: 1;
}

.simplebar-scrollbar.simplebar-visible::before {
  opacity: 1;
}
