*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  position: relative;
  display: flex;
  min-width: 320px;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  background-color: var(--color-black-light);
  color: var(--color-white);
  font-family: var(--font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  line-height: 1.2;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
button {
  padding: 0;
  margin: 0;
}

ul, ol {
  list-style: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  outline: none;

  &:focus {
    outline: none;
  }
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

.main {
  flex-grow: 1;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.lock {
  overflow: hidden;
}

.text-color-fill {
  color: var(--color-brand);
}

.text-uppercase {
  text-transform: uppercase;
}
