@use "../../scss/general/variables";

.steps {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -15px;

  @media (min-width: variables.$lg) {
    margin: 0 -15px;
  }

  @media (min-width: variables.$xl) {
    flex-wrap: nowrap;;
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 15px 30px;

    &:after {
      position: absolute;
      top: 47px;
      right: -70px;
      width: 110px;
      height: 6px;
      background-image: url('../../assets/img/dots.svg');
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }

    @media (min-width: variables.$sm) {
      flex: 1 1 33%;

      &:not(:last-child)::after {
        content: '';
      }

      &:nth-last-child(even)::after {
        content: none;
      }
    }

    @media (min-width: variables.$lg) {
      flex: 0 1 30%;

      &:nth-last-child(even)::after {
        content: "";
      }

      &:nth-last-child(3)::after {
        content: none;
      }
    }

    @media (min-width: variables.$xl) {
      flex: 1 1 20%;

      &:nth-last-child(3)::after {
        content: "";
      }
    }
  }

  &__icon-container {
    display: flex;
    width: 100px;
    height: 100px;
    align-items: center;
    justify-content: center;
    border: 5px solid var(--color-brand-hover);
    margin-bottom: 20px;
    background-color: var(--color-brand);
    border-radius: 50%;
  }

  &__label {
    text-align: center;
  }
}
