@use "../../scss/general/variables";

.payment {
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: -8px;

    @media (min-width: variables.$lg) {
      margin: -12px;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    margin: 8px;
    font-size: 13px;

    @media (min-width: variables.$lg) {
      margin: 12px;
    }
  }

  &__icon {
    flex-shrink: 0;
    margin-right: 10px;
  }

  &__label {
    color: var(--color-gray);
  }
}
