@use "../../scss/general/variables";

.order {
  position: relative;

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('../../assets/img/order-bg.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    content: "";
  }

  &__wrapper {
    position: relative;
    z-index: 10;
    padding: 80px 0;

    @media (min-width: variables.$lg) {
      padding: 120px 0 150px;
    }
  }

  &__title {
    margin-bottom: 24px;
    font-size: 28px;
    font-weight: 800;
    text-align: center;
    text-transform: uppercase;

    @media (min-width: variables.$lg) {
      margin-bottom: 30px;
      font-size: 36px;
    }
  }

  &__subtitle {
    margin-bottom: 56px;
    color: var(--color-gray-light);
    font-size: 16px;
    text-align: center;

    @media (min-width: variables.$lg) {
      margin-bottom: 80px;
      font-size: 18px;
    }
  }

  &__steps {
    margin-bottom: 56px;

    @media (min-width: variables.$lg) {
      margin-bottom: 60px;
    }
  }
}
