/* Breakpoints: */
$xs: 480px;
$sm: 576px;
$md: 768px;
$lg: 1024px;
$xl: 1200px;

:root {
  /* Colors: */
  --color-brand: #3e9cdc;
  --color-brand-hover: #286690;;
  --color-white: #ffffff;
  --color-black: #000000;
  --color-black-light: #272733;
  --color-black-lightest: #3d4050;
  --color-gray-dark: #2a2b30;
  --color-gray-medium: #6f6d7b;
  --color-gray: #c3c3c3;
  --color-gray-light: #bbbbbb;
  --color-error: #d32a20;
  --color-error-light: #ffefef;

  /* Content width: */
  --content-width: 1170px;
  --container-offset: 16px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // Fonts:
  --font: "Lato", "Helvetica", "Arial", sans-serif;
}
