@font-face {
  font-display: swap;
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../assets/fonts/Lato-Regular.woff2") format("woff2"), url("../assets/fonts/Lato-Regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Lato";
  font-style: normal;
  font-weight: 600;
  src: url("../assets/fonts/Lato-Regular.woff2") format("woff2"), url("../assets/fonts/Lato-Regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Lato";
  font-style: normal;
  font-weight: 700;
  src: url("../assets/fonts/Lato-Bold.woff2") format("woff2"), url("../assets/fonts/Lato-Bold.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Lato";
  font-style: normal;
  font-weight: 800;
  src: url("../assets/fonts/Lato-ExtraBold.woff2") format("woff2"), url("../assets/fonts/Lato-ExtraBold.woff") format("woff");
}
/* Breakpoints: */
:root {
  /* Colors: */
  --color-brand: #3e9cdc;
  --color-brand-hover: #286690;
  --color-white: #ffffff;
  --color-black: #000000;
  --color-black-light: #272733;
  --color-black-lightest: #3d4050;
  --color-gray-dark: #2a2b30;
  --color-gray-medium: #6f6d7b;
  --color-gray: #c3c3c3;
  --color-gray-light: #bbbbbb;
  --color-error: #d32a20;
  --color-error-light: #ffefef;
  /* Content width: */
  --content-width: 1170px;
  --container-offset: 16px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --font: "Lato", "Helvetica", "Arial", sans-serif;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  position: relative;
  display: flex;
  min-width: 320px;
  min-height: 100vh;
  flex-direction: column;
  margin: 0;
  background-color: var(--color-black-light);
  color: var(--color-white);
  font-family: var(--font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 400;
  line-height: 1.2;
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
button {
  padding: 0;
  margin: 0;
}

ul, ol {
  list-style: none;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
}
button:focus {
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
}

.main {
  flex-grow: 1;
}

.container {
  max-width: var(--container-width);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.lock {
  overflow: hidden;
}

.text-color-fill {
  color: var(--color-brand);
}

.text-uppercase {
  text-transform: uppercase;
}