@use "../../scss/general/variables";

.social {
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: -8px;

    @media (min-width: variables.$lg) {
      margin: -12px;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    margin: 8px;

    @media (min-width: variables.$lg) {
      margin: 12px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    color: var(--color-white);
    font-size: 13px;
  }

  &__icon {
    flex-shrink: 0;
    margin-right: 10px;
  }

  &__label {
    color: var(--color-white);
    transition: color 0.3s ease-in-out;
  }

  &__link:hover &__label {
      color: var(--color-brand);
    }
}
