@use "../../scss/general/variables";

.header {
  position: fixed;
  z-index: 100;
  top: 0;
  width: 100%;
  background: rgba(16, 16, 29, 0.90);

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 16px 0;

    @media (min-width: variables.$lg) {
      padding: 30px 0;
    }
  }

  &__logo {
    margin-right: 16px;
  }
}
