.select {
  position: relative;
  width: 100%;

  &__btn {
    position: relative;
    display: flex;
    width: 100%;
    height: 48px;
    align-items: center;
    justify-content: space-between;
    padding: 10px 30px 10px 10px;
    border: 1px solid var(--color-whit);
    background-color: rgba(255, 255, 255, 0.85);
    border-radius: 3px;
    color: var(--color-gray-dark);
    cursor: pointer;
    font-size: 18px;
    text-align: left;
    transition: all 0.2s ease-in-out;
  }

  &__arrow {
    position: absolute;
    top: 50%;
    right: 10px;
    pointer-events: none;
    transform: translateY(-50%) rotate(0);
    transition: transform 0.2s ease-in-out;
  }

  &__trigger {
    position: relative;
  }

  &__trigger.is-active {
    .select__btn {
      border: 1px solid var(--color-black-lightest);
      background: rgba(61, 64, 80, 0.85);
      border-radius: 3px 3px 0 0;
      color: var(--color-white);
    }

    .select__arrow {
      fill: var(--color-white);
      transform: translateY(-50%) rotate(180deg);
    }
  }

  &__list {
    position: absolute !important;
    z-index: 100;
    top: 48px;
    left: 0;
    display: none;
    width: 100%;
    max-height: 220px;
    border: 1px solid var(--color-black-lightest);
    background-color: var(--color-black-light);
    border-radius: 0 0 3px 3px;

    &.is-active {
      display: block;
    }
  }

  &__option {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 8px 10px;
    cursor: pointer;
    font-size: 18px;
    text-align: left;
    transition: color 0.3s ease-in-out;

    &:hover,
    &.is-active {
      color: var(--color-brand);
    }
  }
}
